.modal-container{
    position: absolute;
    bottom: 18px;
    background-color: #fff;
    padding: 14px;
    width: 700px;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    

}
.modal-header{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.modal-header button{
    border: 0;
    background: transparent;
}

.modal-container span{
    margin: 14px 0;
    color: #a7a7a7;
    font-size: 18px;
}
.modal-link {
    background-color: rgb(28, 28, 133);
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 14px;
    height: 38px;
    border: 0;
    border-radius: 4px;
    color: #fff;
    cursor: pointer;
}

@media (max-width: 735px){
    .modal-container{
        position: static;
        width: 90%;
        margin-top: 30px;
    }
}